import React, { useState, useEffect } from 'react'
import arrowDownIcon from '../../assets/img/arrow-down.svg'
import styled from 'styled-components'
import classNames from 'classnames';
export default function Dropdown({ selectList = [],onChange }) {
  const [dropdown, setDropdown] = useState(false);
  const [selectedItem, setSelectedItem] = useState(selectList[0].text)
  const selectClass = classNames('dropdown-menu', { 'show': dropdown })
  const onDropdown = () => {
    setDropdown(!dropdown)
  }
  const onSelect = (item) => {
    setSelectedItem(item.text)
    onChange(item.index)
    setDropdown(false)
  }
  const Wrapper = styled.div`
    cursor:pointer;
    .dropdown-menu{
      display:none
    }
    .dropdown-menu.show{
      margin-top:8px;
      background: #203B60;
      display:block;
      border-radius:4px;
      z-index:8;
      position:relative;
      .dropdown-item{
        padding:0 24px;
      }
    }
    .btn{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 24px;
      color:#FFFFFF;

    }
  `
  return (
    <Wrapper>
      <div
        onClick={onDropdown}
        className='btn checked'
      >
        {selectedItem}
        {dropdown ? <img src={arrowDownIcon} alt=''/> : <img src={arrowDownIcon} alt=''/>}
      </div>

      <div className={selectClass}>
        {selectList.map((item, index) => {
          return (
            <div className='dropdown-item' key={index} onClick={() => { onSelect(item) }}>
              {item.text}
            </div>
          )
        })}
      </div>
    </Wrapper>
  )
}

